import React from "react";

function ProjectSpotlight() {
  return (
    <section className="project-spotlight navbar-spacing">
        <div className="container">
            <div className="row">
                <div className="col-lg-4 sidebar">
                    <div className="sidebar-content">
                        <h2>PyQt Tetris</h2>
                        <div>Some date/tag alignment</div>
                        <hr />
                        <h5>Synopsis</h5>
                        <p>Description of what it is</p>
                        <h5>How it works</h5>
                        <p>Some more description</p>
                        <h5>Challenge and Future work</h5>
                        <p>Yada yada yada</p>
                        <h5>Share + Links</h5>
                        <p>Some links</p>
                    </div>
                </div>
                <div className="col-lg-8 gallery">
                    <img src="images/logo-jhu.png" alt="placeholder"/>
                </div>
            </div>
        </div>
    </section>
  );
}

export default ProjectSpotlight;