import React from "react";
import { Link } from "react-router-dom";


function NavBarDropDown(props) {

  if (props.page === "WIP") {
    return (
      <div className="dropdown-menu">
        <a className="dropdown-item" href="/wip">Action</a>
        <a className="dropdown-item" href="/wip">Another action</a>
        <a className="dropdown-item" href="/wip">Something else here</a>
        <div className="dropdown-divider"></div>
        <a className="dropdown-item" href="/wip">Separated link</a>
      </div>
    )
  } else if (props.page === "About") {
    return (
      <div className="dropdown-menu">
        <a className="dropdown-item" href="/">Home</a>
        <a className="dropdown-item" href="/">Education</a>
        <a className="dropdown-item" href="/">Experience</a>
        <a className="dropdown-item" href="/">Skills</a>
    </div>
    )
  } else if (props.page === "Projects") {
    return (
      <div className="dropdown-menu">
        <Link className="dropdown-item" to="/projects/pyqt-tetris">PyQt Tetris</Link>
        <Link className="dropdown-item" to="/projects/pyqt-tetris">Sudoku Solver</Link>
      </div>
    )
  }

}

function NavBarText(props) {
  const listItemClasses = "nav-item dropdown dropdown-mega";
  const linkItemClasses = "nav-link navbar-text-style dropdown-toggle caret-off";

  return (
    <li className={listItemClasses}>
      <div className="nav-collapse">
        <ul className="nav">
          <li className="dropdown">
            <div className="btn-group">

              <Link className={linkItemClasses} to={props.navLink}>
              <button type="button" className="btn btn-navbar btn-sm">{props.navText}</button>
              </Link>
              <button type="button" className="btn btn-navbar dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <span className="sr-only">Toggle Dropdown</span>
                  </button>
                  <NavBarDropDown page={props.navText} />
            </div>


          </li>
        </ul>
      </div>
    </li>
  );
}

function NavBarIcon(props) {
    const listItemClasses = "nav-item";
    const linkItemClasses = "nav-link navbar-icon-style";
  
    return (
      <li className={listItemClasses}>
        <a className={linkItemClasses} href={props.navLink}>
            <i className={props.icon}></i>
        </a>
      </li>
    );
}

function NavBar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark navbar-main fixed-top">
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav">
          <NavBarText navLink="/" navText="About" />
          <NavBarText navLink="/projects" navText="Projects" />
          <NavBarText navLink="/wip" navText="WIP" />
        </ul>
        </div>
        <ul className="navbar-nav ml-auto">
            <NavBarIcon navLink="https://github.com/alanx3x7" icon="fab fa-github"/>
            <NavBarIcon navLink="https://www.linkedin.com/in/alan-lai-0129" icon="fab fa-linkedin"/>
        </ul>
      
    </nav>
  );
}

export default NavBar;
